.detalle-pago-subasta {
  color: #747474;
  background-color: #fff;
  padding: 20px 30px;
  margin-top: 50px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px; }
  .detalle-pago-subasta .subasta-nombre {
    color: #d5a00f;
    margin-top: 0;
