  .detalle-pago-subasta .subasta-cover {
    color: #d5a00f;
    font-size: 30px; }
  .detalle-pago-subasta .subasta-participantes {
    font-size: 20px; }
  .detalle-pago-subasta .banner-data {
    margin: 15px auto; }
  .detalle-pago-subasta .subasta-datos {
    margin-top: 10px; }
