    font-size: 15px;
    color: #747474; }
  .detalle-pago-subasta .subasta-limite span {
    font-weight: bold; }
  .detalle-pago-subasta .subasta-registrados span {
    font-weight: bold; }
  .detalle-pago-subasta .subasta-boton-pago {
    width: 70%;
    margin: 0 auto; }
  .detalle-pago-subasta .caja-contador {
    width: 100%;
    margin: auto; }
  .detalle-pago-subasta .participacion-p,
  .detalle-pago-subasta .ganado-p {
    height: auto;
    margin-top: 10px;
    margin-bottom: 25px; }
